import { Router } from '@angular/router';
import { Component } from '@angular/core';

@Component({
    selector: 'app-map-name',
    templateUrl: 'map-name.component.html',
    styleUrls: ['map-name.component.scss']
})
export class MapNameComponent {
    map = {
        name: '',
        enigma: '',
        rows: 3,
        cols: 3,
    }


    constructor(
        public router: Router
    ) { }


    save() {
        if ((this.map.cols < 16 && this.map.cols > 2) && (this.map.rows < 16 && this.map.rows > 2)) {
            localStorage.setItem('map', JSON.stringify(this.map));
            this.router.navigate(['world']);
        } else {
            alert('Minimo de linhas e colunas 3x3 e máximo 30x30');
        }
    }

}



