import { MasterPlayerService } from './masterPlayer.service';

import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';

@Component({
  selector: 'app-masterPlayer',
  templateUrl: './masterPlayer.component.html',
  styleUrls: ['./masterPlayer.component.scss'],
  providers: [
    MasterPlayerService
  ]
})
export class MasterPlayerComponent implements OnInit {
  user;
  games;
  constructor(
    private route: Router,
    private masterService: MasterPlayerService
  ) { }

  ngOnInit() {
    this.user = JSON.parse(localStorage.getItem('user'));
    this.list();
  }

  list() {
    this.masterService.list(this.user._id)
      .subscribe(data => {
        console.log(data.data)
        this.games = data.data;
      },
      error => console.log(error));

  }

  logout() {
    localStorage.clear();
    this.route.navigate(['/login']);
  }

}


