import { MasterPlayerService } from '../masterPlayer.service';

import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';

@Component({
  selector: 'app-playing',
  templateUrl: './playing.component.html',
  styleUrls: ['./playing.component.scss'],
   providers: [
    MasterPlayerService
  ]
})
export class PlayingComponent implements OnInit {
user;
  games;
  constructor(
    private route: Router,
    private masterService: MasterPlayerService
  ) { }

  ngOnInit() {
    this.user = JSON.parse(localStorage.getItem('user'));
    this.list();
  }

  list() {
    this.masterService.listAll()
      .subscribe(data => {
        console.log(data)
        this.games = data;
      },
      error => console.log(error));

  }

  getData(data){
    window.location.replace('http://localhost:3001/gametime');
    localStorage.setItem('gameData', JSON.stringify(data));
    localStorage.setItem('startPlaying', 'true');
  }

  logout() {
    localStorage.clear();
    this.route.navigate(['/login']);
  }

}


