import { NgModule } from '@angular/core';
import { FormsModule } from '@angular/forms';
import { CommonModule } from '@angular/common';
import { MasterPlayerComponent } from './masterPlayer.component';
import { MasteringComponent } from './mastering/mastering.component';
import { MasterPlayerRoutingModule } from './masterPlayer.routing';
import { PlayingComponent } from './playing/playing.component';

@NgModule({
  imports: [
    CommonModule,
    FormsModule,
    MasterPlayerRoutingModule
  ],
  declarations: [
    MasterPlayerComponent,
    PlayingComponent,
    MasteringComponent
    ]
})
export class MasterPlayerModule { }


