import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { MasterPlayerComponent } from './masterPlayer.component';
import { PlayingComponent } from './playing/playing.component';
import { MasteringComponent } from './mastering/mastering.component';

const routes: Routes = [
    { path: 'playermaster', component: MasterPlayerComponent },
    { path: 'playing', component: PlayingComponent },
    { path: 'mastering', component: MasteringComponent },
];

@NgModule({
    imports: [RouterModule.forChild(routes)],
    exports: [RouterModule],
})
export class MasterPlayerRoutingModule { }



